import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {AdaptModalService} from '../../modal/modal.service';
import {AdaptMessageService} from '../../toast/toast-message.service';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';
import {AdaptAgreementModalOpenEvent, AdaptAgreementState, AdaptUserModalAgreementText} from '../argeement.model';
import {KeyValueObject} from '../../common/common.models';
import {AdaptTranslateService} from '../../common/i18n';
import {formatString} from '../../common/utilities';

@Component({
  selector: 'adapt-agreement-user-modal',
  styleUrls: ['../agreement.scss'],
  templateUrl: './agreement-user-modal.component.html',
  encapsulation: ViewEncapsulation.None
})

export class AdaptAgreementUserModalComponent extends AdaptRadarAngularGenericDirective implements OnChanges {

  isCollapsedPerformanceContent: boolean = true;
  isCollapsedMarketingContent: boolean = true;

  texts: AdaptUserModalAgreementText = {};

  @Input() multiProductUsage: boolean = false;

  @Input() showMarketingSection: boolean = true;

  @Input() state: AdaptAgreementState = {
    accountPerformance: false,
    accountMarketing: false
  };

  @Input() openModal: boolean = false;
  @Input() testID: string;
  @Input() showNotificationAfterSubmit: boolean = true;
  @Input() notificationLinkHandler: () => unknown;

  @Output() readonly stateChange: EventEmitter<AdaptAgreementState> = new EventEmitter<AdaptAgreementState>();
  @Output() readonly submitButtonClick: EventEmitter<AdaptAgreementState> = new EventEmitter<AdaptAgreementState>();
  @Output() readonly modalOpen: EventEmitter<AdaptAgreementModalOpenEvent> = new EventEmitter<AdaptAgreementModalOpenEvent>();

  @ViewChild('mTemplate') mTemplate: TemplateRef<void>;

  private _currentLanguage: KeyValueObject;

  constructor(private _modalService: AdaptModalService,
              private _messageService: AdaptMessageService,
              protected readonly _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>,
              private _ts: AdaptTranslateService) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptAgreementUserModal);

    this._currentLanguage = _ts.getCurrentLanguage();
    this._setDefaultTexts(this._currentLanguage);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['openModal']?.currentValue) {
      this.open();
    }
  }

  handleQualityStateChange(): void {
    if (!this.state.accountPerformance) {
      this.state.accountMarketing = false;
    }

    this.handleStateChange();
  }

  handleStateChange(): void {
    this.stateChange.emit(this.state);
  }

  open(): void {
    const promise = this._modalService
      .open({
        title: null,
        content: this.mTemplate,
        blockKeyboard: true,
        testID: this.testID,
        beforeDismiss: () => false,
      });

    this.modalOpen.emit({promise});
  }

  handleSubmit(close: () => void): void {
    this.submitButtonClick.emit(this.state);

    close();

    if (this.showNotificationAfterSubmit) {
      this._messageService.add({
        severity: 'info',
        summary: this.texts.notificationSummaryText,
        detail: this.texts.notificationDetailsText,
        link: this.texts.notificationLinkText,
        linkHandler: this.notificationLinkHandler,
        life: 10000
      });
    }
  }

  getSubmitButtonText(): string {
    if (this.state.accountPerformance && this.state.accountMarketing) {
      return this.texts.submitButton1;
    }

    if (!this.state.accountPerformance) {
      return this.texts.submitButton2;
    }

    if (!this.state.accountMarketing) {
      return this.texts.submitButton3;
    }
  }

  get learnMoreNoteText(): string {
    return formatString(this._currentLanguage['adapt.agreement.common.learnMoreNote'], this.multiProductUsage ? this._currentLanguage['adapt.agreement.common.learnMoreNoteMultiProductUsage'] : this._currentLanguage['adapt.agreement.common.learnMoreNoteNotMultiProductUsage']);
  }

  private _setDefaultTexts(translation: KeyValueObject): void {
    this.texts.performanceLabel = translation['adapt.agreement.common.performanceLabel'];
    this.texts.marketingLabel = translation['adapt.agreement.common.marketingLabel'];
    this.texts.agreementNote = translation['adapt.agreement.common.marketingLabel'];
    this.texts.learnMoreListItem1 = translation['adapt.agreement.common.learnMoreListItem1'];
    this.texts.learnMoreListItem2 = translation['adapt.agreement.common.learnMoreListItem2'];
    this.texts.learnMoreListItem3 = translation['adapt.agreement.common.learnMoreListItem3'];
    this.texts.learnMoreListItem4 = translation['adapt.agreement.common.learnMoreListItem4'];
    this.texts.learnMoreListItem5 = translation['adapt.agreement.common.learnMoreListItem5'];
    this.texts.learnMoreListItem6 = translation['adapt.agreement.common.learnMoreListItem6'];
    this.texts.learnMoreListItem7 = translation['adapt.agreement.common.learnMoreListItem7'];
    this.texts.learnMoreListItem8 = translation['adapt.agreement.common.learnMoreListItem8'];
    this.texts.learnMoreListItem9 = translation['adapt.agreement.common.learnMoreListItem9'];
    this.texts.learnMoreListItem10 = translation['adapt.agreement.common.learnMoreListItem10'];
    this.texts.learnMoreListItem11 = translation['adapt.agreement.common.learnMoreListItem11'];
    this.texts.learnMoreListItem12 = translation['adapt.agreement.common.learnMoreListItem12'];
    this.texts.learnMoreListItem13 = translation['adapt.agreement.common.learnMoreListItem13'];
    this.texts.learnMoreListItem14 = translation['adapt.agreement.common.learnMoreListItem14'];
    this.texts.mainTitle = translation['adapt.agreement.userModal.mainTitle'];
    this.texts.mainText = translation['adapt.agreement.userModal.mainText'];
    this.texts.loadMoreLabel = translation['adapt.agreement.userModal.loadMoreLabel'];
    this.texts.loadMoreLabel2 = translation['adapt.agreement.userModal.loadMoreLabel2'];
    this.texts.agreementText = translation['adapt.agreement.userModal.agreementText'];
    this.texts.notificationSummaryText = translation['adapt.agreement.userModal.notificationSummaryText'];
    this.texts.notificationDetailsText = translation['adapt.agreement.userModal.notificationDetailsText'];
    this.texts.notificationLinkText = translation['adapt.agreement.userModal.notificationLinkText'];
    this.texts.submitButton1 = translation['adapt.agreement.common.submitButton1'];
    this.texts.submitButton2 = translation['adapt.agreement.common.submitButton2'];
    this.texts.submitButton3 = translation['adapt.agreement.common.submitButton3'];
  }
}
